*{
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
.container{
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding-top: calc((100vh - 816px)/2);
}
#header,#main{
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: .625rem;
  background-color: white;
  height: 816px;
  box-shadow: 0px 0px 4px gray;
}
#header{
  margin-right: 1.5rem;
  width: 360px;
}
#main{
  width: 1200px;
}
header,main{
  height: 100%;
}
header{
  width: 100%;

}
main::-webkit-scrollbar {
  display: none;  /* Ocultar scroll */
}
main{
  display: flex;
  margin: auto;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
  justify-content:space-around;
  flex-wrap: wrap;
}
.top, .bottom{
  z-index: 5;
  width: 100%;
  height: 2rem;
  box-shadow: 0px 0px 5px grey;
}

/* Posts */
li{
	list-style: none;
}
article:nth-child(odd) {
	background-image: url(https://creik.github.io/IndexAnime/background1.jpg);
}
article:nth-child(even) {
	background-image: url(https://creik.github.io/IndexAnime/background2.jpg);
}
article {
	position: relative;
	align-self: stretch;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	/* border-radius: .625em; */
	/* border: 1px solid orange; */
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	color: white;
	/* flex: 1 18.75em; */
	/* min-width: 25.95em; */
	max-width: 23em;
	/* max-height: 15em;
	min-height: 15em; */
	max-height: 14em;
	min-height: 14em;
	box-sizing: content-box;
	margin: .5rem 0;
	padding: .3em .5em;
	box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
}
article .title h4 {
    font-size: 1.5em;
    color: white;
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}
article .info ul {
    display: flex;
    color: white;
    justify-content: space-around;
}
article .info ul li {
    position: relative;
    padding: .2em;
    font-weight: bold;
    /* text-transform: uppercase; */
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}
article .tec {
    visibility: hidden;
    position: absolute;
    margin: auto;
    right: .8em;
    top: .5rem;
    margin-top: -.5em;
    /* transition: all 10s linear; */
}
article .tec ul li {
    margin: .7em 0;
    line-height: 1;
    font-weight: bold;
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}
/* Version 1366px x 768px */

@media screen and (max-width: 1366px){
  .container{
    /* padding-top: 5vh; */ /*first version*/ 
    padding-top: 6.5vh;
  }
  #header,#main{
    /* height: 90vh; */ /*first version*/
    height: 87vh;
  }
  #header{
    width: 300px;
    margin-right: 1rem;
  }
  #main{
    width: 950px;
  }
  article{
    min-width: 18em;
    max-width: 18em;
    max-height: 11em;
    min-height: 11em;
  }
  .info ul li:nth-child(5),.info ul li:nth-child(6){
    display: none;
  }
}






